.bootstrap-datetimepicker-widget {position:fixed;width:100% !important;height:100% !important;left:0 !important;top:0 !important;background:rgba(0,0,0,.6);padding:77px 40px 0 !important;z-index:5000;
	> .row {background:#fff;}
	&:after {display:none !important;}
	.add-on {margin-left:-15px;margin-right:-15px;background:#fff;}
	.picker-switch.accordion-toggle {margin:0 -15px;}
	> .list-unstyled {
		li {background:#fff;}
		li.picker-switch.accordion-toggle {margin:0;}
	}
	.timepicker .timepicker-picker td a {height:42px;}
}