.popup {position:fixed;top:0;left:0;width:100%;height:100%;z-index:9000;background:rgba(0,0,0,.6);display:none;opacity:0;padding:17.5px;
	.popup-content-wrap {width:100%;max-height:100%;min-height:50px;background:#fff;box-shadow:$default-shadow;@include transform(scale(0));overflow:auto;border:solid 1px $primary-color;
		.popup-content {padding:17.5px;min-height:100%;}
	}
	.popup-close {position:absolute;display:block;right:27.5px;top:27.5px;border:solid 1px #ccc;width:30px;height:30px;background:#fff;color:transparent;
		&:after {content:'';@include sprite($m-cross-b);@include transform(scale(.25));position:absolute;top:50%;left:50%;margin-left:-28.5px;margin-top:-29px;}
		&:hover, &:active {border-color:$primary-color;}
	}

	&.ready {display:block;}
	&.open {opacity:1;@include transition(opacity $popupOpenDuration);
		.popup-content-wrap {@include transform(scale(1));@include transition(transform $popupOpenDuration);}
	}
	&.close {opacity:0;@include transition(opacity $popupCloseDuration);
		.popup-content-wrap {@include transform(scale(4));@include transition(transform $popupCloseDuration );}
	}


}