footer {background:url(../imgs/rentking.m/header_bg.jpg) center bottom;background-size:100% auto;width:100%;position:fixed;height:45px;bottom:0;left:0;z-index:1000;box-shadow:$default-shadow;
	ul {
		li {width:25%;float:left;
			a {color:#fff;font-size:10px;display:block;width:100%;height:100%;text-align:center;padding-top:28px;position:relative;
				&:after {content:'';position:absolute;left:50%;top:50%;@include transform(translate3d(0, -6px, 0) scale(.25));}
				&.home:after {@include sprite($m-ico-home);margin-left:-37px;margin-top:-28.5px;}
				&.login:after {@include sprite($m-ico-login);margin-left:-26px;margin-top:-28.5px;}
				&.join:after {@include sprite($m-ico-join);margin-left:-28px;margin-top:-28.5px;}
				&.myinfo:after {@include sprite($m-ico-myinfo);margin-left:-34px;margin-top:-28.5px;}
				&.orderlist:after {@include sprite($m-ico-orderlist);margin-left:-36px;margin-top:-28.5px;}
				&.faq:after {@include sprite($m-ico-faq);margin-left:-37px;margin-top:-28.5px;}

				&:hover, &:active {color:#fff;}
			}
		}
	}
}