//search bar
#searchBar {
	ul {border-top:solid 1px #fff;
		li {border-bottom:solid 1px #fff;position:relative;
			label {display:block;width:100%;height:40px;font-weight:normal;margin:0;color:#fff;
				span {display:block;height:100%;font-size:14px;line-height:40px;
					&.placeholder {float:left;}
					&.value {float:right;padding-right:20.5px;}
				}
				&:after {content:'';@include sprite($m-arrow-bottom-w);@include transform(scale(.25));position:absolute;right:0;top:50%;margin-top:-14.5px;margin-right:-20px;}
			}
			select, input {display:block;width:100%;height:40px;position:absolute;top:0;left:0;}
			select {opacity:0;color:#222;}
			input[type=text] {background:none;border:none;color:#fff;font-size:14px;text-align:right;margin:0;padding-right:20.5px;}
			button[type=submit] {display:block;width:100%;height:40px;color:#fff;border:none;background:$primary-color;font-size:16px;font-weight:600;}

			&.submit {border-bottom:none;padding-top:15px;height:55px;}
		}
	}
}