html.main {min-height:100%;height:100%;background:#222;
	body {min-height:100%;background:url(../imgs/rentking.m/header_bg.jpg) no-repeat center 0 fixed;background-size:100% 100%;}
	header {background:none;}
	#contentWrap {height:100%;padding-bottom:0;}

	.description {color:#fff;text-align:right;margin-top:12.5px;
		.title {font-size:25px;}
		.area {font-size:16px;margin-top:16px;
			.t {font-size:19.5px;}
		}
	}

	#searchBar {margin-top:20px;}

	.data {color:#fff;margin:20px -17.5px 0;
		li {width:25%;float:left;
			span {display:block;text-align:center;font-size:13px;margin-bottom:4px;}
			span.number {font-size:15px;font-weight:600;}
		}
		&:after {content:'';display:block;clear:both;float:none;}
	}
}